import { Col, Row, Tabs, Typography } from "antd";
import React from "react";
import { Products } from "../../data/solutionDetail";
import AttachmentSurat from "../../assets/Surat_Penunjukan_marketing_arm_GTI_ke_IKI_3.pdf";
import PTGerbang from "../../assets/min/pt.gerbang22.png";
import Img from "react-cool-img";
import { Helmet } from "react-helmet";
import query from "query-string";

const { TabPane } = Tabs;

const Product = () => {
  const urlData = query.parse(window.location.search);

  const [TabActive, setTabActive] = React.useState("iot-sensors");

  React.useEffect(() => {
    if (urlData.key) {
      setTabActive(urlData.key);
    }
  }, []);

  console.log(window.location.origin, "HREF");

  return (
    <div className="detail-product-container">
      <Helmet>
        <title>Various IKI IoT solution Products | we-iots</title>
        <meta
          name="description"
          content="Our various products: IoT sensors, smart meter, lora gateway."
        />
        <meta
          name="keywords"
          content="IoT sensors, Smart meter, Lora gateway."
        />
      </Helmet>
      <Row>
        <Col span={24}>
          <div>
            <h1 style={{ margin: "0" }}>IOT Products</h1>
            <div
              style={{
                width: "120px",
                borderTop: "6px solid #ee3326",
                borderRadius: "1px",
              }}
            ></div>
          </div>
        </Col>
      </Row>
      <Tabs
        activeKey={TabActive}
        onChange={(key) => {
          setTabActive(key);
          let url = new URL(window.location.href);
          url.searchParams.set("key", key);

          // console.log(url, "URL");
          window.history.pushState(null, null, url);
        }}
        // onTabChange={(key) => {

        // }}
        style={{ marginTop: "24px" }}
      >
        <TabPane tab={<h2>IoT Sensors</h2>} key="iot-sensors">
          <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
            {Products.filter(
              (val) => val.is_gateway !== true && val.is_smart_meter !== true
            ).map((val, idx) => {
              return (
                <Col
                  className="gutter-row"
                  xl={6}
                  lg={6}
                  md={8}
                  sm={8}
                  xs={12}
                  style={{ margin: "32px 0 0 0" }}
                >
                  <a
                    href={
                      `/${val.pageTarget.split(" ").join("-")}/` +
                      val.keyProduct
                    }
                  >
                    <div
                      key={idx}
                      style={{
                        width: "100%",
                        padding: "16px",
                        border: "1px solid #34488D",
                        borderRadius: "5px",
                        height: "280px",
                        textAlign: "center",
                        display: "flex",
                        flexDirection: "column",
                        justifyContent: "space-between",
                        overflow: "hidden",
                      }}
                    >
                      <Img
                        src={val.img[0]}
                        key={idx}
                        style={{
                          height: "160px",
                          position: "relative",
                          alignSelf: "center",
                        }}
                        alt={val.desc}
                      />
                      <br />
                      <p
                        style={{
                          alignSelf: "center",
                          color: "#000",
                        }}
                      >
                        {val.desc}
                      </p>
                    </div>
                  </a>
                </Col>
              );
            })}
          </Row>
        </TabPane>
        <TabPane tab={<h2>Smart Meter</h2>} key="smart-meter">
          <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
            {Products.filter((val) => val.is_smart_meter === true).map(
              (val, idx) => {
                return (
                  <Col
                    className="gutter-row"
                    xl={6}
                    lg={6}
                    md={8}
                    sm={8}
                    xs={12}
                    style={{ margin: "32px 0 0 0" }}
                  >
                    <a
                      href={
                        `/${val.pageTarget.split(" ").join("-")}/` +
                        val.keyProduct
                      }
                    >
                      <div
                        key={idx}
                        style={{
                          width: "100%",
                          padding: "16px",
                          border: "1px solid #34488D",
                          borderRadius: "5px",
                          height: "280px",
                          textAlign: "center",
                          display: "flex",
                          flexDirection: "column",
                          justifyContent: "space-between",
                          overflow: "hidden",
                        }}
                      >
                        <Img
                          src={val.img[0]}
                          key={idx}
                          style={{
                            height: "160px",
                            position: "relative",
                            alignSelf: "center",
                          }}
                          alt={val.desc}
                        />
                        <br />
                        <p
                          style={{
                            alignSelf: "center",
                            color: "#000",
                          }}
                        >
                          {val.desc}
                        </p>
                      </div>
                    </a>
                  </Col>
                );
              }
            )}
          </Row>
        </TabPane>
        <TabPane tab={<h2>Lora Gateway</h2>} key="lora-gateway">
          <Row>
            <Col xl={8} sm={24} md={12} xs={24}>
              <Typography style={{ fontWeight: 500 }}>
                PT. IoT Kreasi Indonesia cooperates with PT. Gerbang Teknologi
                Indonesia in the procurement of this type of gateway. These
                items are imported by PT. Gerbang Teknologi Informasi, and
                distributed by IKI (PT. IoT Kreasi Indonesia) Legal agreement
                see{" "}
                <a
                  href={AttachmentSurat}
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  attachment
                </a>
                .
              </Typography>
            </Col>
            <Col xl={8} sm={0} md={12} xs={0}>
              <img
                alt="ptgerbang"
                src={PTGerbang}
                style={{ position: "absolute", height: "116px", top: -24 }}
              />
            </Col>
            <Col xl={0} sm={24} md={0} xs={24}>
              <img
                alt="ptgerbang"
                src={PTGerbang}
                style={{ position: "relative", height: "100px" }}
              />
            </Col>
          </Row>
          <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
            {Products.filter((val) => val.is_gateway === true).map(
              (val, idx) => {
                return (
                  <Col
                    className="gutter-row"
                    xl={6}
                    lg={6}
                    md={8}
                    sm={8}
                    xs={12}
                    style={{ margin: "32px 0 0 0" }}
                  >
                    <a
                      href={
                        `/${val.pageTarget.split(" ").join("-")}/` +
                        val.keyProduct
                      }
                    >
                      <div
                        key={idx}
                        style={{
                          width: "100%",
                          padding: "16px",
                          border: "1px solid #34488D",
                          borderRadius: "5px",
                          height: "280px",
                          textAlign: "center",
                          display: "flex",
                          flexDirection: "column",
                          justifyContent: "space-between",
                          overflow: "hidden",
                        }}
                      >
                        <Img
                          src={val.img[0]}
                          key={idx}
                          style={{
                            height: "160px",
                            position: "relative",
                            alignSelf: "center",
                          }}
                          alt={val.desc}
                        />
                        <br />
                        <p
                          style={{
                            alignSelf: "center",
                            color: "#000",
                          }}
                        >
                          {val.desc}
                        </p>
                      </div>
                    </a>
                  </Col>
                );
              }
            )}
          </Row>
        </TabPane>
      </Tabs>
    </div>
  );
};

export default Product;
